"use client";

import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "@/redux/app/store";
import { fetchServices } from "../../../../redux/features/ServicesSlice";

interface Service {
  _id: string;
  serviceName: string;
  serviceCharge: number;
  duration: string;
  serviceDescription?: string;
  image?: string;
}

const ServiceManagement: React.FC = () => {
  const dispatch: AppDispatch = useDispatch();
  const { service, loading, error } = useSelector((state: RootState) => state.service);

  useEffect(() => {
    dispatch(fetchServices());
  }, [dispatch]);

  return (
    <div className="w-full px-4 py-6 bg-[#2B2B2B] min-h-screen">
         <h2 className="text-2xl font-bold text-center text-[#D6D7D6] mb-8">Our Services</h2>
      {/* Loading / Error */}
      {loading && <p className="text-center text-yellow-400 font-semibold">Loading...</p>}
      {error && <p className="text-center text-red-500 font-semibold">{error}</p>}

      {/* Grid */}
      <div className="grid grid-cols-2 gap-6 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 mt-4">
        {Array.isArray(service) && service.length > 0 ? (
          service.map((item: Service) => (
            <div key={item._id} className="flex justify-center">
              <div className="relative w-44 p-3 bg-[#1B1B1A] border border-[#757442] rounded-2xl shadow-xl hover:scale-105 transition-all duration-300 ease-in-out">
                
                {/* Image */}
                <div className="w-full h-32 relative mb-3 overflow-hidden rounded-lg">
                  <img
                    src={item.image || "/placeholder-service.jpg"}
                    alt={item.serviceName}
                    className="w-full h-full object-cover"
                  />
                </div>

                {/* Info */}
                <div className="text-center text-[#D6D7D6] space-y-1">
                  <h6 className="text-md font-bold text-white truncate">{item.serviceName}</h6>
                  <p className="text-sm text-[#757442] font-semibold">â‚¹ {item.serviceCharge}</p>
                  <p className="text-xs text-gray-400">{item.duration}</p>
                  <p className="text-xs text-gray-300 line-clamp-2">{item.serviceDescription}</p>
                </div>
              </div>
            </div>
          ))
        ) : (
          <p className="col-span-full text-center text-gray-500 italic mt-6">
            No services found.
          </p>
        )}
      </div>
    </div>
  );
};

export default ServiceManagement;
